import { useState } from "react";
import Modal from "react-modal";
import {
  HomeOutlined,
  ArrowLeftOutlined,
  ArrowRightOutlined,
  MenuOutlined,
  LineOutlined,
  CloseOutlined,
} from "@ant-design/icons";
Modal.setAppElement("#root");

function App() {
  const [isOpen, setIsOpen] = useState(false);

  const TIME_ELEMENTS = [
    ["7:00 ~ 8:00", "", "QT", "QT", "QT"],
    ["8:00 ~ 9:00", "", "", ""],
    ["9:00 ~ 10:00", "(2)", "(3)", " "],
    ["10:00 ~ 11:00", "(1)", " ", "   "],
    ["11:00 ~ 12:00"],
    ["12:00 ~ 14:00", "", ""],
    ["14:00 ~ 15:00", " ", " ", "(2)"],
    ["15:00 ~ 16:00", "QT & "],
    ["16:00 ~ 17:30", " (1)"],
    ["17:30 ~ 19:00", ""],
    ["19:00 ~ 21:30", ""],
    ["21:30 ~ 22:00", " ", " ", " "],
    ["22:00 ~ ", " (, : 12 )"],
  ];

  function goHompage() {
    window.location.href = "http://www.dfc.kr/";
  }

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div id="main">
      <Modal isOpen={isOpen} contentLabel=" 10 25" id="verse-modal">
        <p>
                    
               <br />  10 25
        </p>
        <label htmlFor="modal-close-button">
          <button onClick={closeModal} id="modal-close-button">
            
          </button>
        </label>
      </Modal>
      <div id="top">
        <div className="window-controll">
          <div className="window-controll-div">
            {/* <LineOutlined />
            <CloseOutlined /> */}
          </div>
        </div>
        <div className="search-bar">
          <ArrowLeftOutlined />
          <ArrowRightOutlined />
          <HomeOutlined />
          <p>www. DFC 22 .co.kr</p>
          <MenuOutlined />
        </div>
      </div>
      <div id="home">
        <div className="time-table-div">
          <div className="time-table">
            <div className="time-table-top">
              <p>   </p>
            </div>
            <table>
              {/* HEAD */}
              <thead>
                <tr className="point">
                  <th></th>
                  <th> </th>
                  <th> </th>
                  <th> </th>
                  <th> </th>
                </tr>
              </thead>

              {/* BODY */}
              <tbody>
                {TIME_ELEMENTS.map((times, idx) => (
                  <tr key={idx}>
                    {times.map((time, i) => {
                      return time.includes("~") ? (
                        <td className="point" key={i}>
                          {time}
                        </td>
                      ) : time === "" ? (
                        <td rowSpan={6} key={i}>
                          {time}
                        </td>
                      ) : time === "(1)" ? (
                        <td rowSpan={2} key={i}>
                          {time}
                        </td>
                      ) : time === "(2)" ? (
                        <td rowSpan={3} key={i}>
                          {time}
                        </td>
                      ) : time.includes("") ? (
                        <td rowSpan={2} key={i}>
                          {time}
                        </td>
                      ) : time === " " ? (
                        <td rowSpan={3} key={i}>
                          {time}
                        </td>
                      ) : time === "   " ? (
                        <td rowSpan={10} key={i}>
                          {time}
                        </td>
                      ) : time.includes("") ? (
                        <td colSpan={3} key={i}>
                          {time}
                        </td>
                      ) : time.includes("") ? (
                        <td colSpan={3} key={i}>
                          {time}
                        </td>
                      ) : (
                        <td key={i}>{time}</td>
                      );
                    })}
                  </tr>
                ))}
              </tbody>
            </table>
            <p className="temp">*     15:30 !</p>
          </div>
        </div>
      </div>
      <div id="bottom">
        <button onClick={goHompage} className="hompage-button">
          Be DFC GROWTH
        </button>
        <button onClick={openModal} className="modal-button">
          <p> 10:25</p>
        </button>
      </div>
    </div>
  );
}

export default App;
